html{

  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans Condensed', sans-serif;
  min-height:100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; /* creates rows out of the elements main and footer */
  height: 100vh; 
  margin:0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.particle-style
{
    
    height: 900px;
    background: black;
   
}

.particle-style h3{
	padding: 0;
	margin: 0;
	font-family: 'Oxygen', sans-serif;
	font-size: 50px;
}
.particle-style h4
{
	padding: 0;
	margin: 0;
	font-family: 'Oxygen', sans-serif;
	font-size: 40px;

}


i {
	border: solid white;
	border-width: 0 3px 3px 0;
	display: inline-block;
	padding: 3px;
	
	
  }
  
.right {
	margin-top: 10px; 
	transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
}

.down {
	margin-left: 10px; 
	transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
}

.knowmore a{
	color: white;
	text-decoration: none;
}
.hover{

	display: none;
	margin-top: 10px; 

}

.no_hover{
	color:white;
	border:2px solid white;
	display: inline-block;
	text-align: center;
	text-decoration: none;
	font-size: 20px;
	padding: 10px 20px;
	margin-top: 20px; 
}
.knowmore:hover .hover{

	background-color:#65b5aa;
	display: inline-block;
	color:white;
	border:2px solid #65b5aa;	
	text-align: center;
	text-decoration: none;
	font-size: 20px;
	padding: 10px 20px;
	margin-top: 20px; 
   
}
.knowmore:hover .no_hover{
	display: none;
	margin-top: 10px; 
}
.text {
	position: absolute;
	top: 50%;
	right: 50%;
	-webkit-transform: translate(50%,-50%);
	        transform: translate(50%,-50%);
	color: white;
	max-width: 90%;
	padding: 2em 3em;
	text-shadow: 0px 0px 2px #131415;
	font-family: 'Open Sans', sans-serif;
}



@media (max-width: 820px) {

    .particle-style{
        height: 800px;
	}
	.text{

		top: 30%;
		right: 30%;
		-webkit-transform: translate(30%,-30%);
		        transform: translate(30%,-30%);


	}

}


  
